.item {
  display: flex;
  width: 100%;
  justify-content: space-between;
  align-items: center;

  .buttonBlock {
    width: 20%;
    display: flex;
    justify-content: space-between;

    @media (max-width: 550px) {
      width: 25%;
    }

    @media (max-width: 440px) {
      width: 30%;
    }

    @media (max-width: 375px) {
      width: 35%;
    }

    button {
      background-color: #eeeeee;
      border-radius: 4px;
      color: #585858;
      font-size: 20px;
      padding: 8px;
      transition: all .5s ease;

      + button {
        margin-left: 5px;
      }

      &:hover {
        background-color: #dedfe1;
      }
    }
  }

  span {
    font-size: 18px;
    line-height: 18px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }
}

.item.completed {
  text-decoration: line-through;
}