.button {
  color: #ffffff;
  background-color: #646ff0;
  font-size: 16px;
  line-height: 20px;
  padding: 5px 10px;
  display: block;
  cursor: pointer;
  border: none;
  outline: none;
  border-radius: 5px;
  transition: all 0.5s ease;

  &:hover {
    background-color: #111b91;
  }
}